import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { fetchMovies } from "../api";
import { QRCodeCanvas } from "qrcode.react";

const Home = () => {
  const [movies, setMovies] = useState([]);
  const [selectedMovie, setSelectedMovie] = useState(null);
  
  useEffect(() => {
    const getMovies = async () => {
      try {
        const response = await fetchMovies();
        console.log(response.data)
        setMovies(response.data);
      } catch (error) {
        console.error("Error fetching movies:", error.message);
      }
    };

    getMovies();
  }, []);
  const handleGenerateQR = (movie) => {
    setSelectedMovie(movie);
  };

  return (
    <div>
      <h1>Movies</h1>
      <ul>
        {movies.map((movie) => (
          <li key={movie._id}>
           <button onClick={() => handleGenerateQR(movie)}>
              {movie.title}
            </button>
          </li>
        ))}
      </ul>
      {selectedMovie && (
        <div>
          <h2>QR Code for {selectedMovie.title}</h2>
          <QRCodeCanvas
            value={`http://localhost:3000/booking/${selectedMovie.id}`}
            size={250}
          />
             <p>QR Code Value: {JSON.stringify({ id: selectedMovie._id, title: selectedMovie.title })}</p>
        </div>
      )}
    </div>
  );
};

export default Home;
