import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { createPayment } from "../api";

const Payment = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const { movie, selectedSeats } = state;

  const handlePayment = async () => {
    try {
      // Step 1: Create a payment order on the backend
      const { data } = await createPayment({
        amount: selectedSeats.length * 100, // Amount in INR (100 per seat)
      });

      // Step 2: Configure Razorpay options
      const options = {
        key: "rzp_test_q46tX5GZGrU1G1", // Replace with your Razorpay Key ID
        amount: data.amount,
        currency: data.currency,
        order_id: data.id, // Razorpay order ID returned from the backend
        name: "Movie Booking App",
        description: `Payment for ${movie.title}`,
        handler: function (response) {
          // Handle successful payment
          console.log("Payment Success:", response);
          alert("Payment Successful!");
          navigate("/");
        },
        prefill: {
          name: "Your Name",
          email: "your.email@example.com",
          contact: "9999999999",
        },
        theme: {
          color: "#3399cc",
        },
      };

      // Step 3: Open Razorpay Checkout
      const rzp = new window.Razorpay(options);
      rzp.open();
    } catch (error) {
      console.error("Error initiating payment:", error.message);
      alert("Payment initiation failed. Please try again.");
    }
  };

  return (
    <div>
      <h1>Payment</h1>
      <p>Movie: {movie.title}</p>
      <p>Seats: {selectedSeats.join(", ")}</p>
      <p>Total Amount: {selectedSeats.length * 100}</p>
      <button onClick={handlePayment}>Pay Now</button>
    </div>
  );
};

export default Payment;
