import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { fetchMovieById, bookSeats } from "../api";
import SeatSelection from "../components/SeatSelection";

const Booking = () => {
  const { id } = useParams();
  const [movie, setMovie] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const getMovie = async () => {
      try {
        const response = await fetchMovieById(id);
        setMovie(response.data);
      } catch (error) {
        console.error("Error fetching movie details:", error.message);
      }
    };

    getMovie();
  }, [id]);

  const handleSeatsSelected = async (selectedSeats) => {
    try {
      // Book the selected seats via API
      await bookSeats({ movieId: id, selectedSeats });

      // Navigate to the payment page with the selected seats
      navigate("/payment", { state: { movie, selectedSeats } });
    } catch (error) {
      console.error("Error booking seats:", error.message);
      alert("Failed to book seats. Please try again.");
    }
  };

  if (!movie) return <p>Loading...</p>;

  return (
    <div>
      <h1>{movie.title}</h1>
      <SeatSelection movie={movie} onSeatsSelected={handleSeatsSelected} />
    </div>
  );
};

export default Booking;
