import axios from "axios";

const API = axios.create({
  baseURL: "https://movie-app-jocb.onrender.com/api", // Update with your backend URL if deployed
});

export const fetchMovies = () => API.get("/movie");
export const fetchMovieById = (id) => API.get(`/movie/show/${id}`);
export const bookSeats = (data) => API.post("/movie/seat", data);
export const addMovie = (data) => API.post("/movie/add", data);
export const createPayment = (data) => API.post("/payment/payment", data);
