import React, { useEffect } from "react";
import { Html5QrcodeScanner } from "html5-qrcode";
import { useNavigate } from "react-router-dom";

const QRScanner = () => {
  const navigate = useNavigate();

  useEffect(() => {
    const qrScanner = new Html5QrcodeScanner(
      "qr-reader",
      { fps: 10, qrbox: { width: 250, height: 250 } },
      false
    );

    const handleSuccess = (decodedText) => {
      try {
        const movieData = JSON.parse(decodedText); // Decode the QR code data
        qrScanner.clear();
        navigate(`/booking/${movieData.id}`, { state: movieData }); // Navigate to booking with movie details
      } catch (error) {
        console.error("Invalid QR Code Data:", error.message);
      }
    };

    qrScanner.render(handleSuccess, console.error);

    return () => {
      qrScanner.clear();
    };
  }, [navigate]);

  return <div id="qr-reader" style={{ width: "300px" }}></div>;
};

export default QRScanner;
